.login{
    background-color: #f0f2f5;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
}
.login__form{


}

h2{
    font-size: 2rem;
    font-weight: 500;
}

.login__field{
    display: flex;
    flex-direction: column;
    gap: .5rem;
    margin-top: 1rem;
    width: 400px;

}

.login__field label{
    
}

.login__field input{
    /* padding: .5rem;
    font-size: inherit;
    font-weight: inherit;
    outline: none;
    border: 1px solid #9f9f9f;
    border-radius: .5rem; */

}

.login__field--checkbox{
    display: flex;
    gap: 2rem;
    margin-top: 1rem;
    cursor: pointer;

}

.login__field--checkbox input{
    width: 25px;
    border: 1px solid #9f9f9f;
    
}

.login__btn{
    width: 100%;
    margin-top: 1rem;
}



.login__link{
    margin-top: .5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login__link a{
    text-decoration: none;
}

.login__link  a:hover{
    color: #257dfd;
}